export class TFModel01 extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];
    selVec: NVector3 | undefined = undefined;

    tfModel: TFModel;

    constructor(tfProjct: TFProject, width: number = 650, height: number = 350) {
        super(tfProjct, width, height);
        this.indeCanvas.title = 'TF Horsepower and Miles Per Gallon';
        this.Init();

    }
    Init() {
        // console.log(tf.version);
        this.tfModel = new TFModel();
    }
    MouseDown(x: number, y: number) {
        const m = new NVector3(this.MapX(x), this.MapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            if (NVector3.Distance(m, this.vs[i]) < 0.05) {
                this.selVec = this.vs[i];
                return;
            }
        }
        this.vs.push(new NVector3(this.MapX(x), this.MapY(y)));
        // console.log(this.vs);
    }
    MouseMove(x: number, y: number) {
        let selVec: undefined | NVector3 = undefined;
        const m = new NVector3(this.MapX(x), this.MapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            let theDis = NVector3.Distance(m, this.vs[i]);
            if (theDis < 0.05) {
                selVec = this.vs[i];
            }
        }
        if (selVec) {
            this.selVec = selVec;
        } else {
            this.selVec = undefined;
        }
    }
    MouseDrag(x: number, y: number) {
        if (this.selVec) {
            this.selVec.x = this.MapX(x);
            this.selVec.y = this.MapY(y);
        }
    }
    MouseUp() {
        this.selVec = undefined;
    }
    MapX(v: number) {
        return MathUtility.Remap(v, 0, this.width, -1, 1);
    }
    MapY(v: number) {
        return MathUtility.Remap(v, 0, this.height, 1, -1);
    }
    GridLine(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = '#555555';
        const xInterval = this.width / 20 - 1;
        const yInterval = this.height / 20 - 1;
        for (let i = 0; i < 20; i += 1) {
            if (i === 10) {
                ctx.lineWidth = 3;
            } else {
                ctx.lineWidth = 1;
            }
            let x0 = xInterval * i;
            let y0 = 0;
            let x1 = x0;
            let y1 = this.height;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();

            x0 = 0;
            y0 = yInterval * i;
            x1 = this.width;
            y1 = y0;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();
        }
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        // ctx.translate(ctx.canvas.width * 0.5, ctx.canvas.height * 0.5);

        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.GridLine(ctx);

        // this.GradientDescent();
        if (this.vs.length > 1) {
            const xx = [], yy = [];
            for (let i = 0; i < this.vs.length; ++i) { xx.push(this.vs[i].x); yy.push(this.vs[i].y); }
            // console.log(xx);
            // console.log(yy);
            // ............................ train
            // this.tfModel.train(xx, yy);
            // ............................ end train

            this.RenderLine(ctx);
        }



        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < this.vs.length; ++i) {
            ctx.beginPath();
            ctx.arc(
                MathUtility.Remap(this.vs[i].x, -1, 1, 0, this.width),
                MathUtility.Remap(this.vs[i].y, -1, 1, this.height, 0),
                5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }

        if (this.selVec) {
            ctx.fillStyle = '#ffffff';
            ctx.font = '15px Arial';
            ctx.textAlign = 'center';
            ctx.fillText(`x:${this.selVec.x.toFixed(3)}, y${this.selVec.y.toFixed(3)}`,
                MathUtility.Remap(this.selVec.x, -1, 1, 0, this.width),
                MathUtility.Remap(this.selVec.y, -1, 1, this.height, 0) - 9,
            );
        }

    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        // const curveX: number[] = [];
        // for (let x = -1; x <= 1; x += 0.05) {
        //     curveX.push(x);
        // }

        // // ............................ predict
        // const ys = tf.tidy(() => {
        //     return this.tfModel.predict(tf.tensor1d(curveX));
        // });

        // let curveY = ys.dataSync();
        // ys.dispose();
        // // ............................ end predict

        // ctx.lineWidth = 3;
        // ctx.strokeStyle = '#00fff0';
        // ctx.beginPath();
        // ctx.moveTo(MathUtility.Remap(curveX[0], -1, 1, 0, this.width), MathUtility.Remap(curveY[0], -1, 1, this.height, 0));

        // for (let i = 1; i < curveX.length; ++i) {
        //     ctx.lineTo(MathUtility.Remap(curveX[i], -1, 1, 0, this.width), MathUtility.Remap(curveY[i], -1, 1, this.height, 0));
        // }
        // ctx.stroke();

    }
}
class TFModel {
    model: tf.Sequential;
    constructor() {
        this.model = tf.sequential();

        const hidden = tf.layers.dense({
            inputShape: [2],
            units: 4,
            activation: 'sigmoid',
        });
        const output = tf.layers.dense({
            units: 1,
            activation: 'sigmoid',

        });

        this.model.add(hidden);
        this.model.add(output);

        const compileArgs: ModelCompileArgs = {
            optimizer: tf.train.sgd(0.1),
            loss: tf.losses.meanSquaredError,
        };

        this.model.compile(compileArgs);

        // const data = tf.tensor2d([
        //     [0.25, 0.92]
        // ]);
        this.train1().then(() => {
            const x = tf.tensor2d([
                [0, 0],
                [0.5, 0.5],
                [1, 1],
            ]);
            const result = this.model.predict(x);
            (result as tf.Tensor).print();
        });
    }
    train0() {
        const x = tf.tensor2d([
            [0, 0],
            [0.5, 0.5],
            [1, 1],
            // [0.1, 0.22],
        ]);
        const y = tf.tensor2d([
            [1],
            [0.5],
            [0],
        ]);
        const config = {
            epochs: 10
        };
        this.model.fit(x, y, config).then((response) => console.log(response.history));
        const outResult: tf.Tensor<tf.Rank> | tf.Tensor<tf.Rank>[] = this.model.predict(x);
        // console.log(outResult);
        (outResult as tf.Tensor).print();
    }
    async train1() {
        const xTrain = tf.tensor2d([
            [0, 0],
            [0.5, 0.5],
            [1, 1],
            // [0.1, 0.22],
        ]);
        const yTrain = tf.tensor2d([
            [1],
            [0.5],
            [0],
        ]);
        const config: ModelFitArgs = {
            epochs: 10,
            shuffle: true,
        };

        for (let i = 0; i < 500; ++i) {
            const response = await this.model.fit(xTrain, yTrain, config);
            // response;
            console.log(i, response.history.loss);
        }

    }
}